export default function SwipePage() {
  const [pets, setPets] = useState(mockPets)
  const [currentIndex, setCurrentIndex] = useState(0)
  const [isLoading, setIsLoading] = useState(false)

  const handleSwipe = async (direction: 'left' | 'right', petId: string) => {
    setIsLoading(true)
    
    try {
      if (direction === 'right') {
        // Simulate API call for liking a pet
        await new Promise(resolve => setTimeout(resolve, 500))
        toast.success(`You liked ${pets[currentIndex].name}!`)
        
        // Simulate match detection (20% chance for demo)
        if (Math.random() < 0.2) {
          toast.success(`It's a match with ${pets[currentIndex].name}! ðŸŽ‰`)
        }
      }
      
      // Move to next pet
      setCurrentIndex(prev => prev + 1)
    } catch (error) {
      toast.error('Something went wrong. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  const handlePass = () => {
    if (currentIndex < pets.length) {
      handleSwipe('left', pets[currentIndex].id)
    }
  }

  const handleLike = () => {
    if (currentIndex < pets.length) {
      handleSwipe('right', pets[currentIndex].id)
    }
  }

  if (currentIndex >= pets.length) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen p-6">
        <div className="text-center">
          <h2 className="text-2xl font-bold mb-4">No more pets to show!</h2>
          <p className="text-gray-600 mb-6">Check back later for more potential matches.</p>
          <Button 
            onClick={() => {
              setCurrentIndex(0)
              setPets([...mockPets])
            }}
            className="bg-primary hover:bg-primary/90"
          >
            Start Over
          </Button>
        </div>
      </div>
    )
  }

  return (
    <div className="flex flex-col items-center justify-center min-h-screen p-4">
      <div className="w-full max-w-sm mx-auto">
        <div className="mb-6 text-center">
          <h1 className="text-2xl font-bold mb-2">Find Your Pet's Match</h1>
          <p className="text-gray-600">Swipe right to like, left to pass</p>
        </div>

        <div className="relative h-[600px] mb-6">
          <AnimatePresence>
            {pets.slice(currentIndex, currentIndex + 2).map((pet, index) => (
              <SwipeCard
                key={pet.id}
                pet={pet}
                onSwipe={handleSwipe}
                isTop={index === 0}
                isLoading={isLoading}
              />
            ))}
          </AnimatePresence>
        </div>

        <div className="flex justify-center space-x-6">
          <Button
            variant="outline"
            size="lg"
            onClick={handlePass}
            disabled={isLoading}
            className="w-16 h-16 rounded-full border-2 border-red-200 hover:border-red-300 hover:bg-red-50"
          >
            <span className="text-2xl">âœ•</span>
          </Button>
          <Button
            size="lg"
            onClick={handleLike}
            disabled={isLoading}
            className="w-16 h-16 rounded-full bg-green-500 hover:bg-green-600"
          >
            <span className="text-2xl">â™¥</span>
          </Button>
        </div>

        <div className="mt-4 text-center text-sm text-gray-500">
          {currentIndex + 1} of {pets.length}
        </div>
      </div>
    </div>
  )
}
